import { useLazyQuery, useQuery } from '@apollo/client'
import { useState, useEffect } from 'react'
import { ALL_BOOKS, BOOKS_WITH_GENRE } from '../queries'
import { Container,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  ButtonGroup,
  Button
} from '@mui/material'

const Books = (props) => {
  const [genre, setGenre] = useState('all genres')
  const [genres, setGenres] = useState([])
  const [books, setBooks] = useState([])
  const { data } = useQuery(ALL_BOOKS)
  const [getBooks] = useLazyQuery(BOOKS_WITH_GENRE, {
    onCompleted: (data) => {
      console.log('get books')
      setBooks(data.allBooks)
    }
  })

  const getAllgenres = (books) => {
    let genreSet = new Set()
    books.forEach(book => {
      book.genres.forEach(genre => genreSet.add(genre))
    });
    return [...genreSet]
  }

  useEffect(() => {
    if(data?.allBooks){
      setBooks(data.allBooks)
      setGenres(getAllgenres(data.allBooks))
    }
  }, [data])

  if (!props.show) {
    return null
  }
  
  const selectGenre = (event) => {
    const text =event.target.innerText.toLowerCase()
    setGenre(text)
    if(text === 'all genres' && data?.allBooks){
      setBooks(data.allBooks)
    }else{
      getBooks({ variables: { genre: text }})
    }
  }

  return (
    <Container>
      <h3>Books</h3>
      <p>in genre {genre}</p>
      <ButtonGroup variant="contained" aria-label="outlined primary button group" style={{ marginTop: "0.5rem" }}>
        {genres.map((genre) => (
          <Button key={genre} onClick={selectGenre}>{genre}</Button>
        ))}
        <Button onClick={selectGenre}>All genres</Button>
      </ButtonGroup>
      <Table style={{ marginTop: "0.5rem" }}>
        <TableHead>
        <TableRow>
            <TableCell style={{ fontWeight: "600" }}>Book Name</TableCell>
            <TableCell style={{ fontWeight: "600" }}>Author</TableCell>
            <TableCell style={{ fontWeight: "600" }}>Published</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {books.map((book) => (
            <TableRow key={book.title}>
              <TableCell>{book.title}</TableCell>
              <TableCell>{book.author.name}</TableCell>
              <TableCell>{book.published}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Container>
  )
}

export default Books
