import { useQuery, useMutation } from '@apollo/client'
import { ALL_AUTHORS, EDIT_AUTHOR } from '../queries'
import { useState } from 'react'
import { Container,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  InputLabel, 
  MenuItem, 
  FormControl, 
  Select, 
  TextField, 
  Button
} from '@mui/material'

const Authors = ({ show, setErrorMessage }) => {
  const [name, setName] = useState('')
  const [year, setYear] = useState('')
  const { data } = useQuery(ALL_AUTHORS)
  const [editAuthor] = useMutation(EDIT_AUTHOR, {
    refetchQueries: [{ query: ALL_AUTHORS }],
    onError: (err) => {
      setErrorMessage(err.graphQLErrors[0].message)
      setTimeout(() => setErrorMessage(null), 5000)
    }
  })

  if (!show) {
    return null
  }

  const authors = data?.allAuthors || []

  const submit = (event) => {
    event.preventDefault()
    editAuthor({ variables: { name, setBornTo: parseInt(year) } })
    setName('')
    setYear('')
  }

  return (
    <Container>
      <h3>Authors</h3>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell style={{ fontWeight: "600" }}>Author</TableCell>
            <TableCell style={{ fontWeight: "600" }}>Born</TableCell>
            <TableCell style={{ fontWeight: "600" }}>Books</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
        {authors.map((a) => (
            <TableRow key={a.name}>
              <TableCell>{a.name}</TableCell>
              <TableCell>{a.born}</TableCell>
              <TableCell>{a.bookCount}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <h3>Set Author Birthyear</h3>
      <FormControl fullWidth>
        <InputLabel id="author-select-label">Please choose an author</InputLabel>
        <Select
          labelId="author-select-label"
          id="author-select"
          value={name}
          onChange={(event) => setName(event.target.value)}
          sx={{ marginTop: "0.5rem" }}
        >
          {authors.map((author) => (
            <MenuItem key={author.name} value={author.name}>
              {author.name}
            </MenuItem>
          ))}
        </Select>
        <TextField 
          placeholder="Author Birth Year" 
          variant="outlined"
          value={year} 
          onChange={(event) => setYear(event.target.value)}
          sx={{ marginTop: "0.5rem" }}
        />
        <Button variant="contained" sx={{ marginTop: "0.5rem" }} onClick={submit}>
          Update Author
        </Button>
      </FormControl>
    </Container>
  )
}

export default Authors
