//import { useState } from 'react'
import { useApolloClient, useMutation } from '@apollo/client'
import { ADD_BOOK, ALL_AUTHORS, ALL_BOOKS, BOOKS_WITH_GENRE } from '../queries'
import { Container, Button } from '@mui/material'
import { ErrorMessage, Field, FieldArray, Form, Formik } from 'formik'
import { InputField, GenreField } from './FormField'

const NewBook = ({ show, setErrorMessage }) => {
  const client = useApolloClient()
  const today = new Date() 

  const [addBook] = useMutation(ADD_BOOK, {
    refetchQueries: [{ query: ALL_AUTHORS }, { query: ALL_BOOKS }],
    update: (cache, response) => {
      const genres = response.data.addBook.genres
      genres.forEach(genre => {
        const data = client.readQuery({ query: BOOKS_WITH_GENRE, variables: {genre}})
        if(data){
          cache.updateQuery({ query: BOOKS_WITH_GENRE, variables: {genre}}, ({ allBooks }) => {
            return{
              allBooks: allBooks.concat(response.data.addBook)
            }
          })
        }
      })
    },
    onError: (error) => {
      setErrorMessage(error.graphQLErrors[0].message)
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    }
  })

  const validate = (values) => {
    const errors = {}
    const RequiredError = ' field is required.'

    if(!values.author){
      errors.author = 'Author' + RequiredError
    }else if(values.author.length < 4){
      errors.author = 'Author should be of minimum 4 characters length'
    }

    if(!values.title){
      errors.title = 'Title' + RequiredError
    }else if(values.title.length < 2){
      errors.title = 'Title should be of minimum 2 characters length'
    }

    if(!values.published){
      errors.published = 'Published' + RequiredError
    }else if(isNaN(parseInt(values.published)) || parseInt(values.published) < 1000 || parseInt(values.published) > today.getFullYear()){
      errors.published = `Please input a valid year number between 1000 to ${today.getFullYear()}`
    }

    if(values.genres.length === 0){
      errors.genres = 'Genres' + RequiredError
    }

    return errors
  }
  const hanleSubmit = async (values, formikBag) => {
    await addBook({ variables: {
      title: values.title.trim(),
      author: values.author.trim(),
      published: parseInt(values.published),
      genres: values.genres
    }})
    formikBag.resetForm()
  }

  if (!show) {
    return null
  }

  return (
    <Container sx={{ width: "50%", marginTop: "1rem" }}>
      <Formik
        initialValues={{
          title: '',
          author: '',
          published: '',
          genres: []
        }}
        validate={validate}
        onSubmit= {hanleSubmit}
      >
        {({ dirty, isValid }) => {
          return (
          <Form>
            <Field name="title" label="Title" component={InputField} />
            <Field name="author" label="author" component={InputField} />
            <Field name="published" label="published" component={InputField} />
            <FieldArray 
              name="genres"
              component={GenreField}
            />
            <ErrorMessage name="genres"/>
            <Button 
              variant="contained" 
              sx={{ width: "100%", marginTop: "0.5rem" }} 
              type="submit"
              disabled={!dirty || !isValid}
            >
              Create Book
            </Button>
          </Form>
        )}}
      </Formik>
    </Container>
  )
}

export default NewBook
