import { useEffect } from 'react'
import { LOGIN } from '../queries'
import { useMutation } from '@apollo/client'
import { Field, Form, Formik } from 'formik'
import { Container, Button } from '@mui/material'
import { InputField } from './FormField'

const LoginForm = ({ show, setToken, setErrorMessage, setMessage, setPage }) => {
  const [login, result] = useMutation(LOGIN, {
    onError: (err) => {
      setErrorMessage(err.graphQLErrors[0].message)
    }
  })

  useEffect(() => {
    if (result.data) {
      const token = result.data.login.value
      setToken(token)
      localStorage.setItem('library-user-token', token)
      setMessage('login successfully')
      setTimeout(() => {
        setMessage('')
        setPage('authors')
      }, 5000)
    }
  }, [result.data]) // eslint-disable-line

  if (!show) return null

  return (
    <Container sx={{ width: "50%", marginTop: "1rem" }}>
      <Formik
        initialValues={{
          username: '',
          password: ''
        }}
        validate={(values) => {
          const errors = {}
          const RequiredError = 'This field is required.'

          if(!values.username){
            errors.username = RequiredError
          }

          if(!values.password){
            errors.password = RequiredError
          }

          return errors
        }}
        onSubmit={(values) => {
          login({ variables: { username: values.username, password: values.password }})
        }}
      >
        {({ dirty, isValid }) => (
          <Form>
            <Field name="username" label="UserName" component={InputField} />
            <Field name="password" label="Password" component={InputField} />
            <Button 
              variant="contained" 
              sx={{ width: "100%", marginTop: "0.5rem" }} 
              type="submit"
              disabled={!dirty || !isValid}
            >
              Login
            </Button>
          </Form>
        )}
      </Formik>
    </Container>
  )
}

export default LoginForm
