import { useEffect, useState, Fragment } from 'react'
import Authors from './components/Authors'
import Books from './components/Books'
import NewBook from './components/NewBook'
import LoginForm from './components/LoginForm'
import Recommend from './components/Recommend'
import { BOOK_SUBSCRIPTION, ALL_BOOKS } from './queries'
import { useApolloClient, useSubscription } from '@apollo/client'
import { ButtonGroup, Button, Alert } from '@mui/material'

const Notify = ({ errorMessage, message }) => {
  if(errorMessage){
    return <Alert severity="error" sx={{ marginTop: "1rem" }}>{errorMessage}</Alert>
  }else if(message){
    return <Alert severity="success" sx={{ marginTop: "1rem" }}>{message}</Alert>
  }else return null
}

export const updateCache = (cache, query, bookAdded) => {
  const uniqueByTitle = (arr) => {
    const set = new Set()
    return arr.filter((item) => {
      let title = item.title
      return set.has(title) ? false : set.add(title)
    })
  }
  cache.updateQuery(query, (data) => {
    return {
      allBooks: uniqueByTitle(data.allBooks.concat(bookAdded)),
    }
  })
}

const App = () => {
  const [page, setPage] = useState('authors')
  const [errorMessage, setErrorMessage] = useState('')
  const [message, setMessage] = useState('')
  const [token, setToken] = useState(null)
  const client = useApolloClient()

  useSubscription(BOOK_SUBSCRIPTION, {
    onSubscriptionData: ({ subscriptionData, client }) => {
      const bookAdded = subscriptionData.data.bookAdded
      setMessage(`a new book '${bookAdded.title}' added`)
      setTimeout(() => {
        setMessage('')
      }, 5000)
      updateCache(client.cache, { query: ALL_BOOKS }, bookAdded)
    },
  })

  useEffect(() => {
    setToken(localStorage.getItem('library-user-token'))
  }, [])

  const logout = () =>{
    setToken(null)
    localStorage.removeItem('library-user-token')
    client.resetStore()
  }

  return (
    <div>
      <ButtonGroup variant="outlined">
        <Button onClick={() => setPage('authors')}>Authors</Button>
        <Button onClick={() => setPage('books')}>Books</Button>
        {token ?
          <Fragment>
            <Button onClick={() => setPage('add')}>Add book</Button>
            <Button onClick={() => setPage('recommend')}>Recommend</Button>
            <Button onClick={logout}>Logout</Button>
          </Fragment>
        : <Button onClick={() => setPage('login')}>Login</Button>}
      </ButtonGroup>
      <Notify errorMessage={errorMessage} message={message}/>
      <Authors show={page === 'authors'} setErrorMessage={setErrorMessage}/>
      <Books show={page === 'books'} />
      <NewBook show={page === 'add'} setErrorMessage={setErrorMessage}/>
      <Recommend show={page === 'recommend'} />
      <LoginForm
        show={page === 'login'}
        setMessage={setMessage}
        setErrorMessage={setErrorMessage}
        setToken={setToken}
        setPage={setPage}
      />
    </div>
  )
}

export default App
