import { Me, BOOKS_WITH_GENRE } from '../queries'
import { useQuery } from '@apollo/client'
import { useEffect, useState } from 'react'
import { Container,
  Typography, 
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody
} from '@mui/material'

const Recommend = (props) => {
  const [genre, setGenre] = useState('')
  const [books, setBooks] = useState([])
  const { data } = useQuery(Me)

  useEffect(() => {
    if(data?.me){
      setGenre(data.me.favoriteGenre)
    }
  }, [data])

  useQuery(BOOKS_WITH_GENRE, {
    variables: { genre },
    skip: !genre,
    onCompleted: (data) => {
      setBooks(data.allBooks)
    },
  })

  if (!props.show) {
    return null
  }

  return (
    <Container>
      <h3>Book Recommendations</h3>
      <Typography variant="h6" component="div">books in your favorite genre: <em>{genre}</em></Typography>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell style={{ fontWeight: "600" }}>Book Name</TableCell>
            <TableCell style={{ fontWeight: "600" }}>Author</TableCell>
            <TableCell style={{ fontWeight: "600" }}>Published</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {books.map((book) => (
            <TableRow key={book.title}>
              <TableCell>{book.title}</TableCell>
              <TableCell>{book.author.name}</TableCell>
              <TableCell>{book.published}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Container>
  )
}

export default Recommend
