import React, { useState } from 'react'
import { ErrorMessage } from 'formik'
import { TextField, Typography, Box, Button } from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete'

export const InputField = ({ field, label }) => (
  <div style={{ marginBottom: "1rem" }}>
    <TextField
      label={label}
      {...field}
      fullWidth
      variant="outlined"
    />
    <Typography variant="subtitle2" style={{ color: "red" }}>
      <ErrorMessage name={field.name} />
    </Typography>
  </div>
);

export const GenreField = ({ push, remove, form }) => {
  const [genre, setGenre] = useState('')

  return (
    <div style={{ marginBottom: "1rem" }}>
      <Box sx={{ marginBottom: ".5rem", display: "flex", alignItems: "stretch", justifyContent: "space-between" }}>
        <TextField 
          variant="outlined"
          value={genre}
          onChange={({ target }) => setGenre(target.value.toLowerCase())}
          label="Genre"
          sx={{ flexGrow: 1 }}
        />
        <Button 
          variant="outlined" 
          onClick={() => {
            if(genre){
              push(genre)
              setGenre('')
            }
          }} 
          sx={{ marginLeft: ".5rem"}} 
          type="button"
        >
          Add Genre
        </Button>
      </Box>
      <div style={{ display: "flex", justifyContent: "flex-satrt", flexWrap: "wrap"}}>
      {form.values.genres.map((genre, index) => {
        return(
        <Button 
          size="small" 
          variant="outlined" 
          key={genre} 
          sx={{ marginRight: ".25rem" }}
          endIcon={<DeleteIcon />}
          onClick={() => remove(index)}
        >{genre}</Button>
        )
      })}
      </div>
    </div>
  )
}